<template>
  <div>
    <router-link :to="{name: 'showEvent', params: {id}}">Back</router-link>
    <div>{{ status }}</div>
    <div>
      <div v-for="guest in guests" :key="guest.id" class="pb-3">
        <div @click="deleteGuest(guest.id)">Delete</div>
        <single-guest :guest="guest"></single-guest>
      </div>
    </div>
  </div>
</template>

<script>
import EventService from '@/services/event.service';
import SingleGuest from '@/components/events/guests/SingleGuest'

export default {
  components: {
    SingleGuest
  },
  data() {
    return {
      guests: [],
      status: ''
    }
  },
  props: {
    id: {
      type: String
    },
  },
  created() {
    this.status = 'Loading...'
    EventService.getGuests(this.id)
      .then(guests=> this.guests = guests)
      .then(()=> this.status = '');
  },
  methods: {
    removeFromGuest(guestIds) {
      if (typeof guestIds == 'number')
        guestIds = [guestIds];
      
      guestIds.forEach(id => {
        
        this.guests.splice(this.guests.findIndex(e=> {
          if (e.id == id)
            return true;
        }), 1);

      });

        
    },
    deleteGuest(guestId) {
      this.removeFromGuest(guestId);
      EventService.removeGuests(this.id, guestId)
        .then(res=> {
          console.log(res.data.data);
        })
        .catch(err=> console.log(err));
    }
  },
}
</script>